body {
  margin: 0;
  line-height: normal;
}

button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  size: 25px;
  color: #fff;
}


:root {
  /* fonts */
  --font-montserrat: Montserrat;
  --font-brigends-expanded: "Brigends Expanded";

  /* font sizes */
  --font-size-xl-7: 20.7px;
  --font-size-mid: 17px;
  --font-size-lg-7: 18.7px;
  --font-size-lg: 18px;
  --font-size-3xl-7: 22.7px;
  --font-size-23xl: 42px;
  --font-size-base: 16px;
  --font-size-7xl-6: 26.6px;
  --font-size-2xl: 21px;
  --font-size-34xl-4: 53.4px;
  --font-size-7xl: 26px;
  --font-size-15xl: 34px;
  --font-size-xl-8: 20.8px;
  --font-size-51xl-7: 70.7px;
  --font-size-38xl: 57px;
  --font-size-lg-8: 18.8px;
  --font-size-40xl-7: 59.7px;
  --font-size-3xs-5: 9.5px;
  --font-size-8xl: 27px;

  /* Colors */
  --color-coral: #fe7f42;
  --color-white: #fff;
  --color-gray-100: #1e1e1e;
  --color-gray-200: rgba(255, 255, 255, 0.6);
  --color-gray-300: rgba(255, 255, 255, 0.2);
  --color-gray-400: rgba(255, 255, 255, 0.1);
  --color-orangered: #fc4d26;
  --color-black: #000;

  /* Gaps */
  --gap-xl: 20px;
  --gap-mini-4: 14.4px;
  --gap-5xl: 24px;
  --gap-31xl: 50px;
  --gap-6xl: 25px;
  --gap-2xs-9: 10.9px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-lg: 18px;
  --padding-10xs: 3px;
  --padding-xl: 20px;
  --padding-2xs: 11px;
  --padding-7xl: 26px;
  --padding-2xl: 21px;
  --padding-6xl: 25px;
  --padding-lgi: 19px;
  --padding-10xl: 29px;
  --padding-6xs: 7px;
  --padding-mini: 15px;
  --padding-11xs: 2px;
  --padding-mid: 17px;
  --padding-30xl: 49px;
  --padding-5xl: 24px;
  --padding-9xs: 4px;
  --padding-4xs: 9px;
  --padding-12xl-4: 31.4px;
  --padding-12xl: 31px;
  --padding-12xl-5: 31.5px;
  --padding-11xl: 30px;
  --padding-8xl-5: 27.5px;
  --padding-sm: 14px;
  --padding-18xl: 37px;

  /* Border radiuses */
  --br-22xl: 41px;
  --br-lgi-2: 19.2px;
  --br-xl: 20px;
  --br-135xl-5: 154.5px;
  --br-85xl: 104px;
}
